.overall-tile{
    width: 405px;
    border-radius: 10px;
    background-color: #7571c7;
  }
  .o-title{
    font-family: Lato;
  font-size: 14px;
  color: #ffffff8c !important;
  }
  .o-count{
    font-family: Lato;
  font-size: 25px;
  font-weight: bold;
  color: #ffffff !important;
  }
  .o-add-btn{
    border-radius: 2px;
    background-color: #fafcff;
    font-family: Lato;
  font-size: 12px;
  color: #363636;
  }
  .o-center{
    line-height: 3.5rem;
    /* height: 200px; */
    /* border: 3px solid green; */
    text-align: right;
  }
  .o-initial-box{
    border-radius: 10px;
    background-color: #00000022;
    display: flex;
  justify-content: center;
  align-items: center;
  
  }
  .o-initial-text{
    font-family: Lato;
    font-size: 2.5rem;
    font-weight: bold;
    color: #ffffff !important;
  }
  .o-oval{
    width: 56px;
  height: 52px;
  background-color: #ffffff1f;
  border-radius: 56px;
  }
  .ant-table thead > tr > th {
    box-shadow: 0 1px 0 0 #0000000e;
    background-color: #fffdfd;
    font-family: Lato;
  font-size: 14px;
  color: #150e4f;
  }